.panel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.qr {
    background-color: rgb(236, 239, 242);
    padding: 30px;
    border-radius: 12px;
}

.instructions {
    text-align: center;
    margin-top: 25px;
}

.instructions-list {
    display: flex;
    width: 580px;
    flex-direction: column;
    align-items: flex-start;
    text-align: initial;
}

.logo {
    margin-top: -50px;
}

.tag {
    margin-bottom: 50px;
}

.paragraphs {
    display: flex;
    justify-content: flex-start;
    list-style-type: disc;
    color: rgb(190, 190, 190);
    width: 550px;
    font-size: 18px;
    line-height: 1.2em;
}

.inc-middleish {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.inc-subheading {
    margin-top: 48px;
}

.inc-btn {
    margin-top: 40px;
    font-family: Source Sans Pro Web,Helvetica Neue,Helvetica,Roboto,Arial,sans-serif;
    font-size: 1.06rem;
    color: #FFF;
    border-radius: 0.25rem;
    border: 2px solid #005ea2;
    background-color: #005ea2;
    padding: 0.75rem 1.25rem;
}

.inc-btn:hover {
    border: 2px solid #005085;
    background-color: #005085;
}